









































































































import { Component, Vue } from 'vue-property-decorator'
import { tablePageMixin } from '@/mixins/tablePageMixin'
import SearchContract from '@/views/business/contract/components/SearchContract.vue'
import { getContractPages } from '@/api/contract/contractApi'
@Component({
  components: {
    SearchContract,
  },
})
export default class ContractPage extends Vue {
  // title页面
  private pageTitle = '租客合同'
  // 搜索条件
  private query = {
    projectId: '', // 城市
    community: '', // 小区名称
    current: 1, // 当前页
    keyword: '', // 关键词
    paymentMethod: '', // 结算周期
    signUserName: '', // 签约人
    size: 10, // size
    state: 0, // 合同状态0、全部1、在租中2、将搬入3、已退租4、待确认
  }
  // 表单数据源
  private dataSource = []
  // 展示按钮文本
  private isConditionText = '收起条件'
  // 分页数据
  private pagination = {
    total: 0,
    current: 1,
  }
  public mounted() {
    this.getContractList()
  }
  /**
   * @Author roct
   * @Description 修改合同状态
   * @Date 20:16 2021/5/19 0019
   **/
  private reloadContractList() {
    this.getContractList()
  }
  /**
   * @Author roct
   * @Description 获取表单数据
   * @Date 19:20 2021/5/19 0019
   **/
  private async getContractList() {
    try {
      this.query = Object.assign(this.query, this.pagination)
      const res = await getContractPages(this.query)
      console.log('res', res.data.records)
      if (res.code === 200) {
        this.dataSource = res.data.records
        this.pagination.total = res.data.total
        this.pagination.current = res.data.current
      } else {
        this.$message.error(res.msg)
      }
    } catch (e) {
      this.$message.error(e.msg || e.message)
    }
  }
  /**
   * @Author roct
   * @Description 修改当前页
   * @Date 20:08 2021/5/19 0019
   **/
  private handleCurrentChange(e: any) {
    console.log('e::', e)
    this.pagination.current = e
    this.getContractList()
  }
  /**
   * @Author roct
   * @Description 点击table的某一行
   * @Date 23:12 2021/5/19 0019
   **/
  private cellClick(scope: any) {
    Vue.ls.set('/contract/detail', scope.row.contract.id)
    location.href = '/index.html/#/contract/detail'
  }
  /**
   * @Author roct
   * @Description 关闭/打开搜索条件
   * @Date 19:18 2021/5/19 0019
   **/
  private toggle() {
    this.isConditionText =
      this.isConditionText === '展开条件' ? '收起条件' : '展开条件'
  }

  setSearchCondition(queryParam: any) {
    this.query = Object.assign(this.query, queryParam)
    this.getContractList()
  }
}
