
































































































import { Component, Mixins, Vue } from 'vue-property-decorator'
import { regionData } from 'element-china-area-data'
import { getDictSelect } from '@/api/manage/sys/housePageApi'
import { getProjectList } from '@/api/manage/sys/houseManageApi'
import '../../house/component/reset.scss'
@Component({
  components: {},
})
export default class SearchContract extends Vue {
  private queryParam: any = {
    projectId: '',
    community: '',
    paymentMethod: '',
    signUserName: '',
  }
  private projectList: any[] = []
  // 支付方式列表
  private payMethodsList: any[] = []
  // 地址
  private optionsArea: any = regionData
  mounted(): void {
    this.getPayMethodsList()
    this.getProject()
  }
  /**
   * @Author roct
   * @Description 获取支付方式列表
   * @Date 22:08 2021/5/19 0019
   **/
  private async getPayMethodsList() {
    const res = await getDictSelect({ dictCode: 'payment_method' })
    if (res.code === 200) {
      this.payMethodsList = res.data
    }
  }
  private async getProject() {
    const res = await getProjectList()
    this.projectList = res.data
  }
  search(): void {
    this.$emit('callBackSearchCondition', this.queryParam)
  }

  resetForm(): void {
    this.queryParam = {}
    this.search()
  }
}
